export default ".select-profile-picture {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 999;\n  opacity: 0;\n  width: 100%;\n  height: 100%;\n  cursor: pointer;\n}\n\n/***avatar hover****/\n\n.hovereffect {\n  width: 50px;\n  height: 50px;\n  float: left;\n  overflow: hidden;\n  position: relative;\n  text-align: center;\n  cursor: default;\n  border-radius: 50%;\n  background-color: #7e35acf5;\n  vertical-align: middle;\n}\n\n.hovereffect .overlay {\n  width: 100%;\n  position: absolute;\n  overflow: hidden;\n  top: 0;\n  left: 0;\n}\n\n.hovereffect img {\n  display: block;\n  position: relative;\n  max-width: none;\n  width: calc(100% + 20px);\n  transition: opacity 0.35s, transform 0.35s;\n  transform: translate3d(-10px, 0, 0);\n  -webkit-backface-visibility: hidden;\n  backface-visibility: hidden;\n}\n\n.hovereffect:hover img {\n  opacity: 0.4;\n  filter: alpha(opacity=40);\n  transform: translate3d(0, 0, 0);\n}\n\n.hovereffect h2 {\n  text-transform: uppercase;\n  color: #7e35acf5;\n  text-align: bottom;\n  font-size: 17px;\n  overflow: hidden;\n  padding: 0.5em 0;\n  background-color: #7e35acf5;\n}\n\n.hovereffect h2:after {\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 2px;\n  background: #7e35acf5;\n  content: \"\";\n  transition: transform 0.35s;\n  transform: translate3d(-100%, 0, 0);\n}\n\n.hovereffect:hover h2:after {\n  transform: translate3d(0, 0, 0);\n}\n\n.hovereffect a, .hovereffect p {\n  color: #7e35acf5;\n  opacity: 0.5;\n  filter: alpha(opacity=0);\n  transition: opacity 0.35s, transform 0.35s;\n  transform: translate3d(100%, 0, 0);\n}\n\n.hovereffect:hover a, .hovereffect:hover p {\n  opacity: 1;\n  filter: alpha(opacity=100);\n  transform: translate3d(0, 0, 0);\n}\n\n.blue-textarea textarea.md-textarea:focus:not([readonly]) {\n  border-bottom: 1px solid #1089CE;\n  box-shadow: 0 1px 0 0 #1089CE;\n  border: none !important;\n}\n\n.image-container {\n  position: relative;\n  display: inline-block;\n  text-align: center;\n}\n\n.image-container:hover .message {\n  transform: translate(-50%, -50%);\n  opacity: 1;\n}\n\n.purpose:hover .edit {\n  display: block;\n}\n\n.purpose {\n  position: relative;\n  display: inline-block;\n}\n\n.edit {\n  /* padding-top: 7px;\t */\n  position: absolute;\n  /* right: 0; */\n  top: 0;\n  display: none;\n}\n\n.edit a {\n  color: #1089CE;\n}\n/*# sourceMappingURL=data:application/json;base64, */"